export const fakeFetch = (response, isError = false, timeout = 500) =>
    new Promise((resolve, reject) => {
        setTimeout(() => {
            if (isError) {
                reject(response);
            } else {
                resolve(response);
            }
        }, timeout);
    });

export const makeFakeSuccessData = (data) => ({data, error: null, code: 200});
export const makeFakeErrorData = (error, errorCode = 400) => ({
    data: null,
    error,
    code: errorCode,
});

export const jsonFetch = (url, ...args) =>
    fetch(url, ...args).then((result) => result.json());
