import TimePicker from "react-time-picker";
import styled from "styled-components";

const Wrapper = styled.div`
  height: 38px;
  border-radius: 6px;
  padding: 5px;
  background-color: #b0b0b0;
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 5px;

  color: #ffffff;

  input,
  input::placeholder {
    color: #ffffff;
  }

  .react-time-picker__inputGroup__input:invalid {
    background: rgba(255, 0, 0, 0.3);
  }

  div[class$="inputGroup"] {
    min-width: fit-content;
  }

  div[class$="wrapper"] {
    border: 0;
  }
`;

function CustomStyleTimePicker(props) {
  return (
    <Wrapper>
      <img src="/clock.png" width={14} height={14} alt="clock" />
      <TimePicker
        {...props}
        format="HH:mm"
        disableClock={true}
        clearIcon={null}
      />
    </Wrapper>
  );
}

export default CustomStyleTimePicker;
