import {Fragment, useCallback, useContext, useEffect, useState,} from "react";
import AuthStore from "../store/AuthStore";
import api from "../api";
import LayoutHeader from "../components/LayoutHeader";
import styled from "styled-components";
import ContentLoader from "react-content-loader";
import ZoomPane from "../components/ZoomPane";
import SeatLayout from "../components/SeatLayout";
import {useSearchParams} from "react-router-dom";

const ReservationLoaderWrapper = styled.div`
  div {
    background-color: #ffffff;
  }
`;

const ReservationLoader = () => (
    <ReservationLoaderWrapper>
        <div>
            <ContentLoader
                width="360"
                height="60"
                foregroundColor="#c0c0c0"
                backgroundColor="#d0d0d0"
                speed="3"
                viewBox="0 0 360 60"
            >
                <rect x="10" y="10" rx="5" ry="5" width="160" height="40"/>
                <rect x="180" y="10" rx="5" ry="5" width="80" height="40"/>
                <rect x="270" y="10" rx="5" ry="5" width="80" height="40"/>
            </ContentLoader>
        </div>
        <ContentLoader
            width="100%"
            height="500"
            foregroundColor="#c0c0c0"
            backgroundColor="#d0d0d0"
            speed="3"
            viewBox="0 0 360 500"
        >
            <rect x="30" y="30" rx="10" ry="10" width="300" height="450"/>
        </ContentLoader>
    </ReservationLoaderWrapper>
);

const StyledDiv = styled.div`
  display: flex;
  flex-direction: column;

  #refresh-button {
    z-index: 9999;
    position: absolute;
    bottom: 2rem;
    left: 1rem;
    color: #5c5c5c;
    font-size: 1rem;
    line-height: 1rem;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
    background-color: #f5f5f5;
    width: 2rem;
    height: 2rem;
  }
`;

function ReservationSeat() {
    const [searchParams] = useSearchParams();
    const {token} = useContext(AuthStore);
    const current = new Date();
    // prettier-ignore
    const currentTime = `${current.getHours() < 10 ? 0 : ""}${current.getHours()}:${current.getMinutes() < 10 ? 0 : ""}${current.getMinutes()}`;

    const [state, setState] = useState({
        focusSeat: searchParams.get("focus") || null,
        loading: true,
        loadingSeats: true,
        error: null,
        layouts: [],
        date: current,
        startTime: currentTime,
        endTime: "19:00",
        layoutId: null,
        layout: null,
        layoutOptions: [],
        defaultOption: null,
        seatsStatus: [],
    });

    const seatOnClick = useCallback((layoutMetadata, seat) => {
        setState(prevState => ({...prevState, focusSeat: null}))
        if (seat.type?.endsWith("SEAT")) {
            if (window.KaGongAndroid) {
                const dto = {
                    layoutId: layoutMetadata.layoutId,
                    seatId: seat.seatId,
                    available: !seat.reserved,
                    startTime: state.startTime,
                    endTime: state.endTime,
                    date: `${state.date.getFullYear()}-${state.date.getMonth() + 1}-${state.date.getDate()}`
                }
                window.KaGongAndroid?.getSeatInformation(JSON.stringify(dto))
            }
        }
    }, []);

    const refreshOnClick = () => {
        if (state.layoutId !== null && state.loadingSeats === false) {
            setState({...state, loadingSeats: true});
            api.getSeatsState(token, state.layoutId).then(({data}) => {
                const seatsStatus = data.seats.reduce((acc, seatStatus) => {
                    acc[seatStatus.seatId] = seatStatus;
                    return acc;
                }, {});
                setState({...state, seatsStatus, loadingSeats: false});
            })
        }
    }

    useEffect(() => {
        api
            .getLayouts()
            .then(({data: {layouts}}) => {
                let defaultOption = null;
                const categories = layouts.reduce((options, layout) => {
                    if (options[layout.layoutMeta.buildingName] === undefined) {
                        options[layout.layoutMeta.buildingName] = [];
                    }

                    const layoutOption = {
                        id: layout.layoutMeta.layoutId,
                        label: layout.layoutMeta.placeName,
                        layout: layout,
                    };

                    if (defaultOption === null) {
                        defaultOption = layoutOption;
                    }

                    options[layout.layoutMeta.buildingName].push(layoutOption);
                    return options;
                }, {});

                const layoutOptions = Object.entries(categories).map(
                    ([key, value]) => ({
                        label: key,
                        options: value,
                    })
                );

                return {
                    layouts: layouts,
                    layoutOptions,
                    layoutId: defaultOption.id,
                    layout: defaultOption.layout,
                    defaultOption,
                };
            })
            .then((newState) =>
                api.getSeatsState(token, newState.layoutId).then(({data}) => {
                    const seatsStatus = data.seats.reduce((acc, seatStatus) => {
                        acc[seatStatus.seatId] = seatStatus;
                        return acc;
                    }, {});
                    setState({...state, ...newState, seatsStatus, loading: false, loadingSeats: false});
                })
            )
            .catch((error) => setState({...state, loading: false, error}));
    }, []);

    return (
        <StyledDiv>
            {state.error === null ? (
                state.loading ? (
                    <ReservationLoader/>
                ) : (
                    <Fragment>
                        <LayoutHeader
                            date={state.date}
                            startTime={state.startTime}
                            endTime={state.endTime}
                            setDate={(date) => setState({...state, date})}
                            setStartTime={(startTime) => setState({...state, startTime})}
                            setEndTime={(endTime) => setState({...state, endTime})}
                            minDate={current}
                            minTime={currentTime}
                            layoutOptions={state.layoutOptions}
                            defaultOption={state.defaultOption}
                            setLayoutOption={({id, layout}) => {
                                setState({
                                    ...state,
                                    layoutId: id,
                                    layout: layout,
                                });
                            }}
                        />
                        <ZoomPane>
                            <SeatLayout
                                focusSeat={state.focusSeat}
                                status={state.seatsStatus}
                                layout={state.layout}
                                seatOnClick={seatOnClick}
                            />
                        </ZoomPane>
                        <button id="refresh-button" onClick={refreshOnClick}>
                            <img alt="refresh" src="/refresh.png" width="14" height="14"/>
                        </button>
                    </Fragment>
                )
            ) : <div>ERROR!!!!!!!</div>}
        </StyledDiv>
    );
}

export default ReservationSeat;
