import styled from "styled-components";

const SeatLayoutWrapper = styled.div`
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  justify-content: center;
  padding-top: 20px;
  z-index: 1;

  .board {
    overflow: hidden;
    position: relative;
    background-color: #fffefc;
    border-radius: 10px;
    box-shadow: 0px 0px 8vw #00000022;

    @keyframes motion {
      0% {
        margin-top: 0px;
      }
      50% {
        margin-top: -10px;
      }
      100% {
        margin-top: 0px;

      }
    }

    .cell {
      position: absolute;
      border-radius: 3px;
      cursor: pointer;

      &.focus {
        margin-top: 0;
        animation: motion 0.5s ease-in-out 0s infinite alternate;
      }

      &.sharing-seat {
        background-color: #bcc4cb;

        &.available {
          background-color: #648cc9;
        }

        &.my-seat {
          background-color: #d1e0f8;

          &::after {
            font-weight: bold;
            font-size: 12px;
            justify-content: center;
            align-items: center;
            display: flex;
            width: 100%;
            height: 100%;
            color: #5f6c7f;
            content: "My";
            text-align: center;
          }
        }
      }

      &.fixed-seat {
        background-color: #e8ebed;

        &::after {
          display: block;
          content: "";
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background-image: url("/fixed.svg");
          background-size: contain;
          background-repeat: repeat;
          background-position: center center;
        }
      }

      &.meeting-room {
        overflow: hidden;
        background-color: #f2f2f2;
        border: 1px dashed #cfcfcf;

        &::after {
          justify-content: center;
          align-items: center;
          display: flex;
          width: 100%;
          height: 100%;
          color: #9c9c9c;
          content: "Meeting";
          text-align: center;
        }

        &.small::after {
          content: "M";
        }
      }
    }
  }
`;

function SeatLayout({layout, seatOnClick, status, focusSeat, zoomToElement}) {
    const seatTypes = {
        MEETING_ROOM: "meeting-room",
        SHARING_SEAT: "sharing-seat",
        FIXED_SEAT: "fixed-seat",
    };

    return (
        <SeatLayoutWrapper>
            <div
                className="board"
                style={{
                    width: layout.layoutMeta.totalWidth,
                    height: layout.layoutMeta.totalHeight,
                }}
            >
                {layout.cells.map((cell) => {
                    const reserved = status[cell.cellId]?.reserved;
                    const mySeat = status[cell.cellId]?.mySeat;
                    return (
                        <div
                            id={`cell-${cell.cellId}`}
                            onClick={() =>
                                seatOnClick(layout.layoutMeta, {
                                    ...status[cell.cellId],
                                    ...cell,
                                })
                            }
                            key={cell.cellId}
                            // prettier-ignore
                            className={`cell ${seatTypes[cell.type]} ${cell.width < 40 ? "small" : ""} ${reserved === false ? "available" : ""} ${mySeat === true ? "my-seat" : ""} ${Number(focusSeat) === cell.cellId ? "focus" : ""}`}
                            style={{
                                fontSize: Math.max(10, Math.min(16, cell.width * 0.2)),
                                top: cell.y,
                                left: cell.x,
                                width: cell.width,
                                height: cell.height,
                            }}
                        />
                    );
                })}
            </div>
        </SeatLayoutWrapper>
    );
}

export default SeatLayout;
