import styled from "styled-components";
import CustomStyleDatePicker from "./CustomStyleDatePicker";
import CustomStyleTimePicker from "./CustomStyleTimePicker";
import Select from "react-select";

const StyledDiv = styled.div`
  background-color: #ffffff;
  display: flex;
  height: 60px;
  padding: 7px 10px;
  gap: 10px;
  align-items: center;
  border-bottom: 1px solid #d0d0d0;

  .select-place {
    max-width: 180px;
    flex: 1;
  }
`;

function LayoutHeader({
                          date,
                          setDate,
                          startTime,
                          setStartTime,
                          endTime,
                          setEndTime,
                          minDate,
                          minTime,
                          layoutOptions,
                          defaultOption,
                          setLayoutOption,
                          className,
                      }) {
    return (<StyledDiv className={className}>
        <Select
            className="select-place"
            isSearchable={false}
            defaultValue={defaultOption}
            onChange={setLayoutOption}
            options={layoutOptions}
        />
        <CustomStyleDatePicker
            value={date}
            minDate={minDate}
            onChange={setDate}
        />
        <CustomStyleTimePicker
            value={startTime}
            minTime={minTime}
            onChange={setStartTime}
        />
    </StyledDiv>);
}

export default LayoutHeader;
